.song-details {
    position: absolute;
    left: 50px;
    /* top: 20px;  */
    line-height: 22px;
}

.song-details .song-name {
    font-family: 'Proxima Nova', Georgia, sans-serif;
    color: rgb(255,255,255);
    font-size: 14px;
}

.song-details .artist-name {
    font-family: 'Proxima Nova', Georgia, sans-serif;
    color: #aaa;
    font-size: 12px;
}

.song-controls {
    -ms-flex-pack: center;
        justify-content: center;
    display: -ms-flexbox;
    display: flex;
    margin-top: 20px;
}

.forward,
.reverse,
.play-btn,
.pause-btn {
    padding: 0 5px;
    color: rgb(179,179,179);
    cursor: pointer;
}

.reverse,
.forward {
    font-size: 16px;
}

.song-player-container .play-btn {
    position: relative;
    top: -7px;
    font-size: 40px;
}

.song-player-container .pause-btn {
    position: relative;
    top: -7px;
    font-size: 40px;
}

.play-btn .fa {
    -webkit-text-stroke: 4px rgb(40,40,40);
}

.pause-btn .fa {
    -webkit-text-stroke: 4px rgb(40,40,40);
}

.song-progress-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.song-progress {
    width: 500px;
    height: 4px;
    background: rgb(64,64,64);
    border-radius: 4px;
}

.song-expired {
    margin: 0;
    /* width: 100px; */
    height: 4px;
    background: #1db954;
    border-radius: 4px;
}

.timer-start {
    font-size: 12px;
    position: relative;
    top: -5px;
    left: -10px;
    color: rgb(255,255,255);
}

.timer-end {
    font-size: 12px;
    position: relative;
    top: -5px;
    left: 10px;
    color: rgb(255,255,255);
}


