body,
p,
h1,
h2,
h3 {
  margin: 0;
  padding: 0;
}

/* Uncomment background and color to change to light mode */
body {
    font-family: 'Proxima Nova', Georgia, sans-serif;
    background: rgb(24,24,24);
    color: rgb(255,255,255);
    /* max-width: 1400px; */
    margin: 0 auto;
    padding: 0;
}

.app-container {
    display: -ms-flexbox;
    display: flex;
    position: relative;
}


